@import "../../../../resources/styles/index.scss";

.Launch {
  @include flex-center-column;
  width: 240px;
  margin: 1.5rem;
  flex-wrap: wrap;

  img {
    width: 80%;
  }

  h3 {
    text-align: center;
    margin: 1rem 0 0.5rem 0;
    color: $blue;
    font-size: 1.7rem;
    font-weight: 100;
    min-height: 65px;
  }

  .Column {
    @include flex-center;
    margin-bottom: 1.2rem;

    svg {
      font-size: 1.3rem;
      margin-left: 0.8rem;
    }

    h4 {
      color: $fontSecondary;
      font-weight: 100;
    }
  }
}

@include tablet {
  .Launch {
    margin: 1rem 0;
    width: 150px;

    h3 {
      font-size: 1.3rem;
      min-height: 40px;
    }

    .Column {
      svg {
        color: $green;
        font-size: 1rem;
        margin-left: 0.8rem;
      }

      h4 {
        font-size: 0.7rem;
      }
    }
  }
}
